import React, { useState, useEffect } from 'react'
import axios from 'axios'
import { Link } from 'react-router-dom'
import TeamCard from './TeamCard'


const Team = () => {

  const [teams, setTeams] = useState([])

  useEffect(()=> {
    const getData = async () => {
      const { data } = await axios('https://www.balldontlie.io/api/v1/teams')
      setTeams(data.data)
    }
    getData()
  },[]) 

  return (
    <>
      <div className="row">
        {teams.length > 0 && teams.map(team => {
          return (
            <div className="teams col-12 col-md-6 col-lg-4 mb-4" key={team.id}>
              <Link to={`/teamcard/${team.id}`}>
                <div className="card-image" id={team.abbreviation}>
                  
                </div>
                <div className="card text-center h-100">
                  <div className="card-header">{team.full_name}</div>
                </div>  
              </Link>
            </div>
          )         
        })}
      </div> 
      {teams.length > 0 && <TeamCard {...teams}/>}
    </>
  )
}

export default Team