import React,{ useState, useEffect } from 'react'
import axios from 'axios'
import { useParams } from 'react-router-dom'



const TeamCard = ({ players }) => {

  const [selectedTeam, setSelectedTeam] = useState(null)

  const { id } = useParams()

  useEffect(()=>{
    const getTeam = async () => {
      const { data } = await axios(`https://www.balldontlie.io/api/v1/teams/${id}`)
      setSelectedTeam(data)
      console.log('team', data)
    }
    getTeam()
  },[id])

  return (
    <>
      {selectedTeam && 
        <><div className="teamheader_container">
          <div className="teamlogo">
            <img src="" alt="" />
          </div>
          <div className="teaminfo">
            <div className="teamheader_side">
              <div className="teamheader_name">
                <div className={selectedTeam.full_name}>{selectedTeam.full_name}</div>
                <div className={selectedTeam.name}>{selectedTeam.name}</div>
                <div className={selectedTeam.abbreviation}>{selectedTeam.abbreviation}</div>
                <div className={selectedTeam.city}>{selectedTeam.city}</div>
                <div className={selectedTeam.conference}>{selectedTeam.conference}</div>
                <div className={selectedTeam.division}>{selectedTeam.division}</div>
              </div>
            </div>
          </div>
        </div>
        <div className="players_container">
          <div className="playerinfo_title">
            <h2>Roster</h2>
          </div>
          <div className="roster_content">
            <div className="playerstat_table">
              <table className="table table-hover">
                <thead className="thead-light">
                  <tr>
                    <th>Player</th>
                    <th>Position</th>
                    <th>Height</th>
                    <th>weight</th>
                  </tr>
                </thead>
                <tbody>
                  {players.length > 0 && players.map(player => {
                    console.log('player', player)
                    if (player.team.id === selectedTeam.id){
                      return (
                        <tr key={player.id}>
                          <td>{player.first_name} {player.last}</td>
                          <td>{player.position}</td>
                          <td>{player.height_feet}feet {player.height_inches} inches</td>
                          <td>{player.weight}</td>
                        </tr>
                      ) 
                    }  
                  })}
                </tbody>
              </table>
            </div>
          </div>
        </div></>
      }
    </>
    
  )
}

export default TeamCard